<template>
  <div>
    <div class="module filter">
      <div class="l">
        <el-input placeholder="请输入内容" v-model="form.keyword">
          <template slot="prepend">搜索主体</template>
        </el-input>
      </div>

      <div class="l">
        <el-button @click="getList()">搜索</el-button>
      </div>
    </div>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="系统ID" width="100"> </el-table-column>

        <el-table-column prop="name" label="种类名称">
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                管理<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <router-link :to="{path:'/iot/cropsclass/detail/', query:{rowId: updateTable.id}}">
                  <el-dropdown-item @click.native="detail(scope.row)">详情</el-dropdown-item>
                </router-link>
                <el-dropdown-item @click.native="updatedia(scope.row)">修改</el-dropdown-item>
                <el-dropdown-item @click.native="dele(scope.row.id)">删除</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>

      <el-dialog title="修改种类" :visible.sync="dialogVisible" width="30%" :show-close='false' :close-on-click-modal='false'>
        <div class="module">
          <div class="mtitle">种类名称</div>
          <el-input v-model="updateTable.name" placeholder="种类名称"></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click.native="updateList(1)">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <div class="module">
      <Pagination @pagination="getList()" :page.sync="form.querypage" :pageCount.sync="totalPage" :total.sync="total"
        v-if="total > 0" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { Mainlist, Addmainspecies, Managermainspecies } from "@/api/cropsdata";
import Pagination from "@/components/Pagination.vue";
export default {
  Addmainspecies,
  name: "Index",
  data() {
    return {
      module1: {
        subject_count: 0,
        nowday_data_newadd_count: 0,
        gateway_count: 0,
        equipment_count: 0,
        opendata_usecount: 0
      },
      form: {
        keyword: "",
        querypage: 1
      },
      tableData: [], // 列表
      total: 0, // 总条目
      totalPage: 0, // 总页数
      currentPage: 1, // 当前页码

      dialogVisible: false,

      updateTable: []
    };
  },
  components: {
    Pagination
  },
  methods: {
    getList() {
      this.$http(
        Mainlist({
          querypage: this.currentPage
        }),
        res => {
          if (res.code == 200) {
            this.$message({
              message: res.msg,
              type: "success"
            });
            this.total = res.data.total;
            this.currentPage = res.data.currentPage;
            this.totalPage = res.data.totalPages;
            this.tableData = res.data.result;
          } else {
            this.$message({
              message: res.msg,
              type: "error",
              duration: 5 * 1000
            });
          }
        }
      );
    },
    updatedia(item) {
      this.dialogVisible = true;
      this.updateTable = item;
    },
    dele(id) {
      this.$confirm('', '这是一段内容确定删除吗？', () => {
        //删除接口
        this.$http(
          Managermainspecies({
            id: id,
            set: 1
          }),
          res => {
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success"
              });
              this.getList();
            } else {
              this.$message({
                message: res.msg,
                type: "error",
                duration: 5 * 1000
              });
            }
          }
        );
      }, () => {
        this.$message('已取消')
      })
    },
    detail(item) {
      this.updateTable = item;
    },
    updateList(type) {
      console.log(type);
      if (type == 1) {
        this.$http(
          Addmainspecies({
            id: this.updateTable.id,
            name: this.updateTable.name
          }),
          res => {
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success"
              });

              this.dialogVisible = false;
            } else {
              this.$message({
                message: res.msg,
                type: "error",
                duration: 5 * 1000
              });
            }
          }
        );
      }
    },
  },
  created() {
    this.getList();
  },
};
</script>
<style lang="scss" scoped>
</style>